@font-face {
    font-family: 'DGUVMetaWeb';
    font-style: normal;
    font-weight: 400;
    src: url(../fonts/dguvmetaweb-normal.woff?#iefix) format("embedded-opentype"), url(../fonts/dguvmetaweb-normal.woff) format("woff");
}

@font-face {
    font-family: 'DGUVMetaWeb';
    font-style: italic;
    font-weight: 400;
    src: url(../fonts/dguvmetaweb-normalita.woff?#iefix) format("embedded-opentype"), url(../fonts/dguvmetaweb-normalita.woff) format("woff");
}

@font-face {
    font-family: 'DGUVMetaWeb';
    font-style: normal;
    font-weight: 600;
    src: url(../fonts/dguvmetaweb-bold.woff?#iefix) format("embedded-opentype"), url(../fonts/dguvmetaweb-bold.woff) format("woff");
}

@font-face {
    font-family: 'DGUVMetaWeb';
    font-style: italic;
    font-weight: 700;
    src: url(../fonts/dguvmetaweb-boldita.woff?#iefix) format("embedded-opentype"), url(../fonts/dguvmetaweb-boldita.woff) format("woff");
}

@font-face {
  font-family: "axel";
  src:url("../fonts/axel/Axel-Regular.eot");
  src:url("../fonts/axel/Axel-Regular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/axel/Axel-Regular.woff") format("woff"),
    url("../fonts/axel/Axel-Regular.ttf") format("truetype"),
    url("../fonts/axel/Axel-Regular.svg#winter") format("svg");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "axel";
  src:url("../fonts/axel-bold/Axel-Bold.eot");
  src:url("../fonts/axel-bold/Axel-Bold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/axel-bold/Axel-Bold.woff") format("woff"),
    url("../fonts/axel-bold/Axel-Bold.ttf") format("truetype"),
    url("../fonts/axel-bold/Axel-Bold.svg#winter") format("svg");
  font-weight: 600;
  font-style: normal;
}

